.App {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  height: calc(100vh - 2em);
  width: calc(100vw - 2em);
  padding: 1em;
  background-color: #282c34;
}

.App > div {
  width: 30vw;
}

.board-row {
  display: flex;
  justify-content: center;
  align-items: center;
}

.status {
  color: #61dafb;
  margin-bottom: 10px;
}

.square {
  background: #fff;
  border: 1px solid #999;
  font-size: calc(10vw * 0.8); /* 80% of the square size */
  font-weight: 100;
  font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
  line-height: 10vw;
  height: 10vw;
  margin: 0;
  padding: 0;
  text-align: center;
  transition: background-color 0.3s ease;
  width: 10vw;
}

.square:focus {
  outline: none;
}

.square img {
  width: 100%;
}

button {
  cursor: pointer;
  font-size: 15px;
  margin-top: 15px;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  transition: all 0.3s ease;
}

@keyframes pulse {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}

.x-turn button:not(.restart):active {
  background-color: #dddddd;
  animation: pulse 0.2s linear;
}

.restart {
  width: 100%;
  cursor: pointer;
  font-size: 24px;
  padding: 1em;
  border: none;
  background-color: #666666;
  color: white;
  border-radius: 5px;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  animation: fadeOut 2s forwards;
}

.modal div {
  position: fixed;
  top: 22%;
  left: 50%;
  width: 70%;
  max-width: 30vh;
  transform: translate(-50%, -50%);
  padding: 10px 20px 15px 20px;
  background-color: #ffffff;
  box-shadow: 0 0 5px 1px #666666;
  color: #666666;
  font-size: 2em;
  border-radius: 10px;
}

@keyframes fadeOut {
  0% {
    opacity: 1;
    pointer-events: all;
  }
  40% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    pointer-events: none;
  }
}


@media (max-width: 600px) {
  .square {
    font-size: calc(30vw * 0.8); /* 80% of the square size */
    height: 30vw;
    line-height: 30vw;
    width: 30vw;
  }

  .App > div {
    width: 100vw;
  }

  .restart {
    max-width: 100vw;
  }
}